<template>

    <header :class="{'active': isActive}">
        <nav class="s-menu">
            <ul>
                <li><router-link to="/">Home</router-link></li>
                <li><router-link to="/login">Login</router-link></li>
            </ul>
        </nav>
        <nav class="gnb">
            <button type="button" class="allBtn" @click="menuOpen">
                <span><b class="hidden">전체메뉴</b></span>
            </button>
            <div class="gnb-menu">
                <ul class="gnb-menu-item">
                    <li><button data-route="SubPage01" @click="handleClick" class="btn">vue</button></li>
                    <li><button data-route="TabView01" @click="handleClick" class="btn">tab-v1</button></li>
                    <li><button data-route="TabView02" @click="handleClick" class="btn">tab-v2</button></li>
                    <li><button data-route="ModalView01" @click="handleClick" class="btn">modal-1</button></li>
                    <li><button data-route="ModalView02" @click="handleClick" class="btn">modal-2</button></li>
                    <li><button data-route="AccoView" @click="handleClick" class="btn">accordion</button></li>
                    <li><button data-route="SelectBoxView" @click="handleClick" class="btn">selectbox</button></li>
                    <li><a href="#" data-route="SelectBoxView" @click="handleClick">링크이동</a></li>
                </ul>
            </div>
        </nav>
    </header>

</template>
<script setup>
import { ref } from 'vue';
import { useRouter } from 'vue-router';
const router = useRouter();
const isActive = ref(false);

const menuOpen = (e) => {
    isActive.value = !isActive.value;

    const _this = e.target;
    const _menuWrap = _this.closest('.gnb');  
    const _menuItem = _menuWrap.querySelector('.gnb-menu');

    if(isActive.value) {
        _menuItem.style.display = 'flex';

        const menuH = _menuItem.scrollHeight;
        _menuItem.style.height = menuH + 'px';
    } else {
        _menuItem.style.height = 0;
    }
}
const handleClick = (e) => {
    const routeName = e.target.dataset.route; // data-route 속성에서 라우트 이름 가져오기
    router.push({ 
        name: routeName,
        params: {
            member: 'a',
        },
        state: {
            // studentBasicInfo: {
                // id:  studentBasicInfo.id,
                // name: studentBasicInfo.name
            // }
        }
    });  // 라우트 이름으로 이동
}
</script>